//display
.d-none(){
    display: none;
}
//borders
.b-red(){
    border: 1px solid red;
}

//margin and padding
.ma(){
    margin: 0 auto;
}
.mp0(){
    margin: 0;
    padding: 0;
}

.mlr(){
    margin-left: 0;
    margin-right: 0;
}
.mlr-a(){
    margin-left: auto;
    margin-right: auto;
}

.mlr-15(){
    margin-left: -15px;
    margin-right: -15px;
}

//font-weight
.fwn(){
    font-weight: normal;
}

.fwb(){
    font-weight: bold;
}

