.mobileMenuBtn {
    display: none;

    .to(860px, { display: block; width: 20px; height: 20px; position: absolute; right: 10px; top: 17px; z-index: 500000; cursor: pointer; background-color: transparent; transition: background-color ease .5s, border-radius linear .2s; });
}

.mobileMenuBtn.active {
    background-color: transparent;
    border-radius: 50%;
}

.mobileMenuBtn.active .iconLine {
    background-color: transparent;
    transform: rotate(360deg);
    transition: background-color ease-in .3s,
    transform linear .4s;
}

.mobileMenuBtn.active .iconLine:after,
.mobileMenuBtn.active .iconLine:before {
    transition: top ease .5s, transform ease .4s;
}

.mobileMenuBtn.active .iconLine:after {
    background-color: #000;
    transform: translateY(15px) rotate(-45deg);
    top: -15px;
}

.mobileMenuBtn.active .iconLine:before {
    background-color: #000;
    transform: translateY(15px) rotate(45deg);
    top: -15px;
}

.iconLine {
    display: block;

    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;

    width: 15px;
    height: 1px;

    background-color: #000;

    transition: transform ease 0.3s;
}

.iconLine:after,
.iconLine:before {
    content: '';
    width: 15px;
    height: 1px;

    background-color: #000;

    display: block;

    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.iconLine:after {
    top: 6px;
    /*left: -1px;*/
}

.iconLine:before {
    top: -6px;
    /*left: -1px;*/
}

