/*Стили слайдера*/
/*стилизация кнопок слайдера*/
.mainSlider .slick-dots {
    width: 20%;
    right: 0;
    bottom: 6.6%;
    top: inherit;
    left: 13.3%;

    margin-bottom: 0;

    .to(1200px, { left: 10%; });
    .to(1070px, { left: 8%; });
    .to(960px, { left: 4%; });
}

.mainSlider .slick-dots li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 6px;
    padding: 0;
    cursor: pointer;
}

.mainSlider .slick-dots li button {
    border: 1px solid @white;
    border-radius: 50%;
    width: 18px;
    height: 18px;
}

.mainSlider .slick-dots li button:before { /*не активная кнопка*/
    content: ' ';
    width: 18px;
    height: 18px;
    border-radius: 50%;
    opacity: 1;
    //border: 1px solid @white;
    background-color: transparent;

    top: 0;
    left: 0;
    right: 0;
    //margin: auto;
    bottom: 0;
    margin: 0 0 0 0;
}

.mainSlider .slick-dots li.slick-active button {
    border: 1px solid @white;
}

.mainSlider .slick-dots li.slick-active button:before {
    //opacity: 1;
    //color: @white;
    background-color: @white;
}

/*стрелки слайдера*/
.mainSlider .slick-prev:before {
    /*content: '←';*/
    content: url(../img/arrL.png);
}

.mainSlider [dir='rtl'] .slick-prev:before {
    /*content: '→';*/
    content: url(../img/arrR.png);
}

.mainSlider .slick-next:before {
    /*content: '→';*/
    content: url(../img/arrR.png);
}

.mainSlider [dir='rtl'] .slick-next:before {
    /*content: '←';*/
    content: url(../img/arrL.png);
}

/*Позиционирование стрелок*/
.mainSlider .slick-prev {
    left: 7.7%;
    top: 44%;
    z-index: 1001;

    .to(1250px, { left: 1%; });
}

.mainSlider .slick-next {
    top: 44%;
    right: 8.5%;

    .to(1250px, { right: 2%; });
}

