    flex-direction: column;

    max-width: 400px;

    .loraReg();
    color: @black34;

    p {
        position: relative;
        text-align: center;
        font-size: 36px;

        margin-bottom: 49px;
        .mlr-a();

        &:before {
            display: inline-block;

            position: absolute;
            bottom: -17px;
            left: 0;

            content: " ";
            width: 125px;
            height: 2px;
            background-color: @green98;

            .to(430px, { display: none; });
        }
    }
    input {
        max-width: 400px;
        height: 45px;
        border: 1px solid @greyCC;

        .latoReg();
        font-size: 14px;

        color: @black34;
        opacity: 1;

        outline: none;

        padding-left: 18px;

        margin-bottom: 23px;
        &:last-child {
            margin-bottom: 0;
        }
        &::placeholder {
            color: @black34;
            opacity: 1;
        }
        &:-moz-placeholder {
            color: @black34;
            opacity: 1;
        }
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: @black34;
            opacity: 1;
        }
        &:-ms-input-placeholder { /* IE 10+ */
            color: @black34;
            opacity: 1;
        }
        &:-moz-placeholder { /* Firefox 18- */
            color: @black34;
            opacity: 1;
        }
    }


}
/*ie10+ */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .form {width: 100%;}
}

.form .formSubmit {
    cursor: pointer;
    margin-top: 9px;

    .mlr-a();
    text-align: center;
    padding-left: 0;
    padding-top: 0;

    .latoReg();
    font-size: 18px;
    color: @white;

    background-color: @green7c;
    width: 249px;
    border-radius: 25px;
}

