  .titleBlock h2:before {
    left: 5px;
  }

  .titleBlock h1:after,
  .titleBlock h2:after {
    right: 5px;
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .form {
    width: 100%;
  }

  .contactsBlock__left,
  .contactsBlock__right {
    width: 100%;
  }
}